.element-quiz-5 {
  padding-top: 30px;
  padding-bottom: 30px;
  overflow: hidden;
  position: relative;
}

.element-quiz-5-container {
  transition: all 0.3s ease-out;
  width: 100%;
  max-width: 760px;
  margin: auto;
  position: relative;
  background: #ffffff;
  overflow: hidden;
  border: 1px solid #ddd;
  box-shadow: 0 0.4rem 1.2rem 0.2rem rgba(0, 0, 0, 0.05);
  padding: 35px 35px 58px 35px;
}

.element-quiz-5-question {
  border-bottom: 1px solid #f2f2f2;
  padding: 10px;
  line-height: 1.42;
}

.choice-answered {
  border-radius: 0 !important;
}

.element-quiz-5-question .editor strong {
  font-weight: bold;
}

.element-quiz-5-question .editor em {
  font-style: italic;
}

.element-quiz-5-question .editor u {
  text-decoration: underline;
}

.element-quiz-5-header {
  margin-bottom: 10px;
}
.element-quiz-5-header span {
  font-weight: 600;
  text-transform: uppercase;
}

.element-quiz-5-header span:last-child {
  float: right;
  letter-spacing: 2px;
}

/* Navigation style */

.rdp-react-quiz-5-navigation {
  background: #f9f9f9;
  width: 100%;
  height: 50px;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
}

.rdp-react-quiz-5-navigation-progress {
  width: 300px;
  height: 50px;
  margin: 0 auto;
}

.rdp-react-quiz-5-navigation-progress-bar-placeholder {
  width: 260px;
  height: 6px;
  border-radius: 50px;
  background: #ccc;
  margin-top: 23px;
  display: inline-block;
}

.rdp-react-quiz-5-navigation-progress-bar {
  width: 0;
  height: 6px;
  border-radius: 50px;
  background: #2d7b45;
  transition: all 0.3s ease-out;
}

.rdp-react-quiz-5-navigation-progress-count {
  font-weight: bold;
  font-size: 11px;
  margin-left: 5px;
  color: #2d7b45;
  letter-spacing: 2px;
}

.rdp-react-quiz-5-result {
  height: 450px;
  overflow: hidden;
}

.rdp-react-quiz-5-result-icon {
  width: 100px;
  height: 100px;
  display: block;
  margin: 50px auto 30px;
  background-size: 100%;
}

.rdp-react-quiz-5-result-message {
  color: #6b6b6b;
  font-size: 20px;
  font-weight: 1000;
  font-family: Lato-Bold, serif;
  display: block;
  text-align: center;
  line-height: 30px;
}


.element-quiz-5-feedback-container {
  padding: 20px 0;
  transition: all 0.3s ease-out;
  width: 100%;
}

.element-quiz-5-feedback-msg {
  transition: all 0.3s ease-out;
  text-align: center;
  line-height: 1.42;
}

.element-quiz-5-feedback-msg p {
  margin: 10px auto;
  font-weight: 500;
  color: #333;
}

@media screen and (max-width: 500px) {
  .element-quiz-5-container {
    padding: 15px 15px 108px 15px;
    margin: 15px;
    width: auto;
  }

  .rdp-react-quiz-5-navigation {
    height: 100px;
  }

  .rdp-react-quiz-5-navigation-progress {
    text-align: center;
  }

  .rdp-react-quiz-5-navigation-progress-bar-placeholder {
    width: 230px;
  }
}

.rdp-react-quiz-5-stats-container {
  width: 100%;
  padding: 10px;
}

.rdp-react-quiz-5-stats-container-content {
  display: flex;
  justify-content: center;
}

.rdp-react-quiz-5-stats-container {
  width: 100%;
  padding: 10px;
}

.rdp-react-quiz-5-stats-container-content {
  display: flex;
  justify-content: center;
}
