.element-3d {
  padding-top: 30px;
  padding-bottom: 30px;
  border-top: 1px solid transparent;
  border-bottom: 1px solid transparent;
  width: 100%;
  display: block;
  margin: auto;
  overflow: hidden;
  position: relative;
}

.element-3d:hover {
  border-top: 1px dashed #ccc;
  border-bottom: 1px dashed #ccc;
}

.element-3d-area {
  display: block;
  width: 100%;
  max-width: 760px;
  min-height: 400px;
  margin: auto;
}

#default-poster {
  background-color: transparent!important;
}