.rdp-element-quote-4 {
  width: 100%;
  background-color: #FFFFFF;
  text-align: center;
  background-size: 100%;
  position: relative;
  overflow: hidden;
  /* border-top: 1px solid transparent; */
  /* border-bottom: 1px solid transparent; */
}

.rdp-element-quote-4-container {
  width: 100%;
  max-width: 760px;
  display: block;
  margin: auto;
  padding: 50px 0px;
  text-align: left;
  margin: 30px auto;
}

.mobile .rdp-element-quote-4-container,
.tablet .rdp-element-quote-4-container {
  padding: 30px;
}

.rdp-element-quote-4-text-container {
  position: relative;
  width: calc(100% - 100px);
  display: inline-block;
  vertical-align: top;
  margin-left: 30px;
}

.mobile .rdp-element-quote-4-avatar,
.tablet .rdp-element-quote-4-avatar {
  width: 60px;
  height: 60px;
}

.mobile .rdp-element-quote-4-text-container,
.tablet .rdp-element-quote-4-text-container {
  width: calc(100% - 80px);
  margin-left: 20px;
}

.rdp-element-quote-4-text {
  color: #fff;
  width: 100%;
  display: block;
}

.rdp-element-quote-4-text .editor {
  padding: 0;
  line-height: inherit;
  overflow: hidden;
}

.rdp-element-quote-4-text .editor p:before,
.rdp-element-quote-4-text .editor p:after {
  content: '"';
  color: #313537;
  font-size: 16px;
}

.rdp-element-quote-4-text .editor * {
  line-height: 150%;
}

.rdp-element-quote-4-avatar {
  width: 70px;
  height: 70px;
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
  position: relative;
  z-index: 1;
  border-radius: 500px;
}

.rdp-element-quote-4-avatar img {
  margin-left: 50%;
  margin-top: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.rdp-element-quote-4-author-info {
  display: block;
  margin-top: 10px;
}

.rdp-element-quote-4-author-name {
  color: #fff;
  font-family: Prompt-Regular;
  font-style: normal;
  font-weight: 700;
  line-height: 26px;
  display: inline-block;
  vertical-align: middle;
}

.rdp-element-quote-4-author-name .editor {
  padding: 0;
  line-height: inherit;
  overflow: hidden;
  font-size: 17px;
}

.rdp-element-quote-4-author-name .editor * {
  font-family: Lato-Regular!important;
}

.rdp-element-quote-4:hover {
  /* border-top: 1px dashed #ccc; */
  /* border-bottom: 1px dashed #ccc; */
}

@media screen and (max-width: 760px) {
  .rdp-element-quote-4-container {
    padding: 30px;
  }

  .rdp-element-quote-4-avatar {
    width: 60px;
    height: 60px;
  }

  .rdp-element-quote-4-text-container {
    width: calc(100% - 80px);
    margin-left: 20px;
  }
}