.element-iframe {
  padding-top: 30px;
  padding-bottom: 30px;
  border-top: 1px solid transparent;
  border-bottom: 1px solid transparent;
  width: 100%;
  display: block;
  margin: auto;
  overflow: hidden;
  position: relative;
}

