body {
  margin: 0;
  /* font-family: TitilliumWeb-Regular; */
  font-family: Prompt;
  background-color: #FFFFFF;
}

.input-upload {
  width: 100%;
  background: #e5e5e5;
  height: 90px;
  line-height: 90px;
  text-align: center;
  margin-bottom: 20px;
  color: #616161;
  cursor: pointer;
  position: relative;
  
  background: #f6f6f6;
  margin-top: 10px;
  margin-bottom: 40px;
}

.input-upload input {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  cursor: pointer;
  height: 100%;
  opacity: 0;
}

.input-label {
  display: block;
  margin-bottom: 6px;
  font-size: 13px;

  margin-bottom: 0;
}

input {
  width: 100%;
  height: 50px;
  margin-bottom: 20px;
  padding-left: 15px;
  border: 1px solid #e3e3e3;
  /* border-radius: 5px; */

  background: transparent;
  border: 0;
  border-bottom: 2px solid #d5d5d5;
  padding-left: 0px;
  margin-bottom: 40px;
}

.rdp-h3 {
  font-size: 20px;
  margin-bottom: 30px;
  margin-top: 10px;
  color: #3d3d3d;
  font-weight: bold;
}

.quill {
  background: #fff;
  min-height: 180px;
  margin-top: 15px;
  margin-bottom: 35px;
}

.ql-toolbar.ql-snow + .ql-container.ql-snow {
  min-height: 140px;
}

.ql-snow .ql-tooltip {
  top: 9px!important;
  left: 0!important;
  width: 100%;
  box-shadow: none;
  border-left: none;
  border-right: none;
}

.ql-snow .ql-tooltip.ql-editing a.ql-action::after {
  content: "Salvar"!important;
}

.ql-snow .ql-tooltip.ql-editing input[type=text]::placeholder {
  color: transparent;
}

.ql-snow .ql-tooltip[data-mode=audio]::before {
  content: "URL do Áudio:"!important;
}

.ql-snow .ql-tooltip[data-mode=video]::before {
  content: "URL do Vídeo:"!important;
}

.ql-snow .ql-tooltip[data-mode=link]::before {
  content: "Insira o link:"!important;
}

.editor strong {
  font-weight: bold;
}

.editor em {
  font-style: italic;
}

.ql-toolbar.ql-snow .ql-formats {
  margin-right: 0!important;
}