.ql-align-center {
  text-align: center;
}

.rdp-element-text-content * {
  line-height: 150%;
}

.rdp-element-text-content {
  width: 100%;
  max-width: 760px;
  margin: auto;
  font-size: 18px;
  line-height: 120%;
}

.rdp-element-text-content strong {
  font-weight: bold;
}

.rdp-element-text-content em {
  font-style: italic;
}

.rdp-element-text-content u {
  text-decoration: underline;
}

.rdp-element-text-content h1 {
  font-size: 30px;
  line-height: 40px;
  margin-bottom: 10px;
  font-weight: bold;
}

.rdp-element-text-content h2 {
  font-size: 20px;
  line-height: 30px;
  font-weight: bold;
}

.rdp-element-text-content ul li {
  list-style: disc;
  margin-left: 40px;
}

.rdp-element-text {
  padding-top: 35px;
  padding-bottom: 35px;
  overflow: hidden;
  position: relative;
}

.rdp-element-text textarea, .rdp-element-text div.editor {
  width: 100%;
  max-width: 760px;
  display: block;
  margin: auto;
  border: 0;
  font-size: 18px;
  line-height: 26px;
  resize: none;
  overflow: hidden;
}

.rdp-element-text div.editor b {
  font-weight: bold;
}

.rdp-element-text .quill {
  width: 100%;
  max-width: 760px;
  margin: auto;
}

@media screen and (max-width: 768px) {
  .rdp-element-text {
    padding-left: 20px;
    padding-right: 20px;
  }
}