.rdp-composer-player-container.userlog {
  width: calc(100% - 300px);
}

.rdp-composer-player-container.has-header {
  padding-top: 70px;
}

.rdp-composer-player-user-log {
  width: 300px;
  background: white;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 100;
  overflow-x: hidden;
  overflow-y: auto;
  box-shadow: -6px 0px 16px rgb(126 126 126 / 30%);
}

.rdp-composer-player-user-log li {
  font-family: Lato-Regular;
  padding: 15px;
  border-bottom: 1px solid #efefef;
  font-size: 14px;
  line-height: 20px;
  color: #787878;
}

.rdp-composer-player-user-log li b {
  font-family: Lato-Bold;
  font-weight: bold;
  color: #000;
}

.rdp-composer-player-user-log-date {
  display: block;
  margin-bottom: 8px;
}

.rdp-composer-player-user-log-date time {
  background: #515151;
  color: #fff;
  font-weight: bold;
  font-family: Lato-Bold;
  padding: 4px 9px;
  border-radius: 50px;
  font-size: 11px;
}

.rdp-composer-player-header {
  position: fixed;
  width: 100%;
  height: 70px;
  background-color: #FFFFFF;
  box-shadow: 0px 0px 10px rgb(0 0 0 / 20%);
  z-index: 10;
  top: 0px;
}

.rdp-composer-player-header-title {
  font-weight: bold;
  display: block;
  text-align: center;
  height: 70px;
  line-height: 70px;
  color: #3f3f3f;
  text-transform: uppercase;
}

.rdp-composer-player-header-index-menu-btn {
  position: fixed;
  z-index: 9999;
  background-color: #FFF;
  width: 70px;
  height: 70px;
  border-radius: 100px;
  right: 0px;
  top: 0px;
  cursor: pointer;
  text-align: center;
}

.rdp-composer-player-header-index-menu-btn.left {
  left: 0px;
}

.rdp-composer-player-header-index-menu-btn.right {
  right: 0px;
}

.rdp-composer-player-header-index-menu-btn svg {
  height: 100%;
  width: 40px;
}

.rdp-composer-player-index-menu {
  position: fixed;
  width: 300px;
  top: 70px;
  bottom: 0px;
  right: 0px;
  background-color: #FFFFFF;
  transition: all 0.3s linear;
  overflow-x: hidden;
  overflow-y: auto;
  border-radius: 0;
  z-index: 1;
  overflow-x: hidden;
  overflow-y: auto;
  box-shadow: 0px 0px 10px rgb(0 0 0 / 20%);
}

.rdp-composer-player-index-menu-item {
  cursor: pointer;
  border-bottom: 1px solid #efefef;
  position: relative;
  padding: 10px 0px 10px 21px;
}

.rdp-composer-player-index-menu-item.active {
  background: #ededed;
  font-weight: bold;
}

.rdp-composer-player-index-menu-item-title {
  font-size: 14px;
  display: inline-block;
  margin-left: 14px;
  /* margin-top: 3px; */
  font-weight: 700;
  color: #353535;
  width: calc(100% - 85px);
  line-height: 19px;
  vertical-align: top;
}

.rdp-composer-player-index-menu-item-bullet {
  width: 25px;
  height: 25px;
  display: inline-block;
  text-align: center;
  line-height: 25px;
  background: #f0f0f0;
  color: #888888;
  border-radius: 8px;
  font-size: 12px;
  font-weight: 700;
  vertical-align: top;
}

.rdp-composer-player-index-menu-item.active .rdp-composer-player-index-menu-item-bullet {
  background: #353535;
  color: #ededed;
}

.rdp-composer-player-index-menu-item-duration {
  width: 100%;
  display: block;
  margin-left: 40px;
  font-size: 10px;
  color: #989898;
}

.rdp-composer-player-prev-btn,
.rdp-composer-player-next-btn,
.rdp-composer-player-done-btn {
  width: 100%;
  height: 70px;
  background-color: #f0f0f0;
  cursor: pointer;
}

.rdp-composer-player-prev-btn-label,
.rdp-composer-player-next-btn-label,
.rdp-composer-player-done-btn-label {
  color: #313537;
  display: block;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 12px;
}

.rdp-composer-player-next-btn-label,
.rdp-composer-player-done-btn-label {
  padding-top: 18px;
}

.rdp-composer-player-prev-btn-icon {
  display: block;
  width: 30px;
  height: 36px;
  background-size: 100%;
  margin: auto;
  background-position: center;
  background-image: url("./icon-up.svg");
}

.rdp-composer-player-next-btn-icon {
  display: block;
  width: 30px;
  height: 36px;
  background-size: 100%;
  margin: auto;
  background-position: center;
  background-image: url("./icon-down.svg");
}

.rdp-composer-player-done-btn {
  background-color: #96efb5;
  cursor: default;
}

.rdp-composer-player-done-btn-label {
  color: #2d7b45;
}

.rdp-composer-player-done-btn-icon {
  display: block;
  width: 30px;
  height: 36px;
  background-size: 100%;
  margin: auto;
  background-position: center;
  background-image: url("./icon-done.svg");
}

@media screen and (max-width: 768px) {
  .rdp-composer-player-index-menu {
    width: 100%;
  }
}