.element-quiz-2 {
  padding-top: 30px;
  padding-bottom: 30px;
  /* border-top: 1px solid transparent; */
  /* border-bottom: 1px solid transparent; */
  overflow: hidden;
  position: relative;
}

.element-quiz-2-container {
  transition: all 0.3s ease-out;
  width: 100%;
  max-width: 760px;
  margin: auto;
  position: relative;
  background: #ffffff;
  overflow: hidden;
  border: 1px solid #ddd;
  box-shadow: 0 0.4rem 1.2rem 0.2rem rgba(0, 0, 0, 0.05);
  padding: 35px 35px 58px 35px;
}

.element-quiz-2-question {
  border-bottom: 1px solid #f2f2f2;
  padding: 10px;
  line-height: 1.42;
}

.element-quiz-2-choices {
  margin-top: 0px;
  padding-top: 10px;
}

.choice-answered {
  border-radius: 0px !important;
  /* box-shadow: 1px 1px 4px #6666; */
}
.element-quiz-2-choice {
  transition: all 0.3s ease-out;
  border: 1px solid transparent;
  /* border-radius: 50px; */
  border-radius: 10px;
  margin: 10px 0px;
  cursor: pointer;
  padding-top: 8px;
  padding-bottom: 8px;
}

.element-quiz-2-choice:hover {
  background: #f8f8f8;
  /* border-radius: 50px; */
  border-radius: 10px;
}

.element-quiz-2-choice-radio {
  width: 60px;
  height: 45px;
  display: inline-block;
  vertical-align: middle;
  position: relative;
}

.element-quiz-2-choice-radio::before {
  content: "";
  background: transparent;
  width: 20px;
  height: 20px;
  border-radius: 0px;
  display: block;
  margin: 12px auto;
  border: 1px solid #ddd;
}

.element-quiz-2-correct-choice {
  content: "";
  background-color: transparent;
  width: 12px;
  height: 12px;
  border-radius: 100px;
  display: block;
  position: absolute;
  top: 16px;
  left: 24px;
}

.element-quiz-2-choice .quill {
  width: calc(100% - 60px);
  display: inline-block;
  vertical-align: middle;
}

.element-quiz-2-choice .quill p {
  cursor: pointer;
}

.element-quiz-2-question .editor strong,
.element-quiz-2-choice .editor strong {
  font-weight: bold;
}

.element-quiz-2-question .editor em,
.element-quiz-2-choice .editor em {
  font-style: italic;
}

.element-quiz-2-question .editor u,
.element-quiz-2-choice .editor u {
  text-decoration: underline;
}

.element-quiz-2-choice-text {
  display: inline-block;
  width: calc(100% - 85px);
  vertical-align: middle;
  line-height: 1.42;
}

.element-quiz-2-image {
  width: calc(100% - 60px);
  margin: 20px;
}

.element-quiz-2-video {
  width: calc(100% - 60px);
  margin: 20px;
}

.element-quiz-2-header {
  margin-bottom: 10px;
}
.element-quiz-2-header span {
  font-weight: 600;
  text-transform: uppercase;
}

.element-quiz-2-header span:last-child {
  float: right;
  letter-spacing: 2px;
}

/* Navigation style */

.rdp-react-quiz-navigation {
  background: #f9f9f9;
  /* border-top: 1px solid #2d7b45; */
  width: 100%;
  height: 50px;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
}

.rdp-react-quiz-navigation-previous-btn {
  background: transparent;
  color: #2d7b45;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 11px;
  border: 0;
  padding: 10px 20px;
  border-radius: 2px;
  position: absolute;
  left: 0;
  top: 10px;
  cursor: pointer;
  font-family: Lato-Bold;
}

.rdp-react-quiz-navigation-next-btn,
.rdp-react-quiz-navigation-finish-btn {
  background: transparent;
  color: #2d7b45;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 11px;
  border: 0;
  padding: 10px 20px;
  border-radius: 2px;
  position: absolute;
  right: 0;
  top: 10px;
  cursor: pointer;
  font-family: Lato-Bold;
}

.rdp-react-quiz-navigation-progress {
  width: 300px;
  height: 50px;
  margin: 0px auto;
}

.rdp-react-quiz-navigation-progress-bar-placeholder {
  width: 260px;
  height: 6px;
  border-radius: 50px;
  background: #ccc;
  margin-top: 23px;
  display: inline-block;
}

.rdp-react-quiz-navigation-progress-bar {
  width: 0px;
  height: 6px;
  border-radius: 50px;
  background: #2d7b45;
  transition: all 0.3s ease-out;
}

.rdp-react-quiz-navigation-progress-count {
  font-weight: bold;
  font-size: 11px;
  margin-left: 5px;
  color: #2d7b45;
  letter-spacing: 2px;
}

.rdp-react-quiz-result {
  height: 450px;
  overflow: hidden;
}

.rdp-react-quiz-result-icon {
  width: 100px;
  height: 100px;
  display: block;
  margin: 50px auto 30px;
  /* background-image: url("./icon-success.png"); */
  background-size: 100%;
}

.rdp-react-quiz-result-message {
  color: #6b6b6b;
  font-size: 20px;
  font-weight: 1000;
  font-family: Lato-Bold;
  display: block;
  text-align: center;
  line-height: 30px;
}

.rdp-react-quiz-remake-btn {
  transition: all 0.3s ease-out;
  cursor: pointer;
  padding: 6px;
  width: fit-content;
  display: block;
  margin: 40px auto;
  text-align: center;
  border: 1px solid transparent;
  border-radius: 4px;
}

.rdp-react-quiz-remake-btn:hover {
  border: 1px dashed #f1f1f1;
}

.rdp-react-quiz-remake-btn-icon {
  width: 25px;
  height: 38px;
  display: block;
  margin: auto;
  /* background-image: url("./icon-refresh.svg"); */
  background-size: 100%;
  text-align: center;
}

.rdp-react-quiz-remake-btn-text {
  margin-top: 10px;
  font-size: 13px;
  font-weight: 600;
}

.element-quiz-2-feedback-container {
  transition: all 0.3s ease-out;
  padding: 20px 0px;
  transition: all 0.3s ease-out;
  width: 100%;
}
.element-quiz-2-btn-confirm {
  transition: all 0.3s ease-out;
  display: block;
  margin: 0px auto;
  background: #2d7b45;
  color: #fff;
  border: 0;
  padding: 8px 30px;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  border-radius: 30px;
  cursor: pointer;
}

.element-quiz-2-feedback-msg {
  transition: all 0.3s ease-out;
  text-align: center;
  line-height: 1.42;
}

.element-quiz-2-feedback-msg p {
  margin: 10px auto;
  font-weight: 500;
  color: #333;
}

@media screen and (max-width: 500px) {
  .element-quiz-2-container {
    padding: 15px 15px 108px 15px;
    margin: 15px;
    width: auto;
  }

  .rdp-react-quiz-navigation {
    height: 100px;
  }

  .rdp-react-quiz-navigation-previous-btn,
  .rdp-react-quiz-navigation-next-btn,
  .rdp-react-quiz-navigation-finish-btn {
    top: 60px;
  }

  .rdp-react-quiz-navigation-progress {
    text-align: center;
  }

  .rdp-react-quiz-navigation-progress-bar-placeholder {
    width: 230px;
  }
}
