.rdp-element-quote-1 {
  width: 100%;
  background-color: #FFFFFF;
  text-align: center;
  background-size: 100%;
  position: relative;
  overflow: hidden;
  /* border-top: 1px solid transparent; */
  /* border-bottom: 1px solid transparent; */
}

.rdp-element-quote-1-container {
  width: 100%;
  max-width: 760px;
  display: block;
  margin: auto;
  padding: 50px 0px;
  text-align: center;
  border-top: 1px solid #e9eaeb;
  border-bottom: 1px solid #e9eaeb;
  margin: 30px auto;
}

.mobile .rdp-element-quote-1-container,
.tablet .rdp-element-quote-1-container {
  padding: 30px;
}

.rdp-element-quote-1-text-container {
  position: relative;
}

.rdp-element-quote-1-text {
  color: #fff;
  margin-top: 30px;
  width: 100%;
  display: block;
}

.rdp-element-quote-1-text .editor {
  padding: 0;
  line-height: inherit;
  overflow: hidden;
}

.rdp-element-quote-1-text .editor p:before,
.rdp-element-quote-1-text .editor p:after {
  content: '"';
  color: #313537;
  font-size: 16px;
}

.rdp-element-quote-1-text .editor * {
  line-height: 150%;
}

.rdp-element-quote-1-avatar {
  width: 70px;
  height: 70px;
  border-radius: 500px;
  overflow: hidden;
  display: block;
  margin: auto;
  box-shadow: 0px 4px 7px rgba(0, 0, 0, 0.4);
}

.rdp-element-quote-1-avatar img {
  margin-left: 50%;
  margin-top: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.rdp-element-quote-1-author-info {
  display: block;
  margin-top: 10px;
}

.rdp-element-quote-1-author-name {
  color: #fff;
  font-family: Prompt-Regular;
  font-style: normal;
  font-weight: 700;
  line-height: 26px;
  display: inline-block;
  vertical-align: middle;
}

.rdp-element-quote-1-author-name .editor {
  padding: 0;
  line-height: inherit;
  overflow: hidden;
}

.rdp-element-quote-1-author-name .editor * {
  font-family: Lato-Regular!important;
}

.rdp-element-quote-1:hover {
  /* border-top: 1px dashed #ccc; */
  /* border-bottom: 1px dashed #ccc; */
}

@media screen and (max-width: 760px) {
  .rdp-element-quote-1-container {
    padding: 30px;
  }

  .rdp-element-quote-1-text-container-icon {
    width: 10px;
    height: 10px;
    position: absolute;
    left: -20px;
  }
}