.rdp-element-text-two-column .ql-toolbar.ql-bubble {
  border: 0;
}
.rdp-element-text-two-column .ql-container.ql-bubble {
  /* position: absolute; */
  width: 100%;
  /* top: -30px; */
  transition: all 0.2s linear;
  border: 0;
}

.rdp-element-text-two-column .ql-toolbar {
  /* opacity: 0; */
  /* padding: 8px 0px; */
}

/* .rdp-element-text-two-column:hover .ql-toolbar  {
  opacity: 1;
} */

.rdp-element-text-two-column .ql-editor {
  font-size: 18px;
  /* line-height: 26px; */
  line-height: 150%;
  padding: 0;
  overflow: hidden;
}

.rdp-element-text-two-column .ql-editor strong {
  font-weight: bold;
}

.rdp-element-text-two-column .ql-editor em {
  font-style: italic;
}

.rdp-element-text-two-column .ql-editor u {
  text-decoration: underline;
}

.rdp-element-text-two-column .ql-bubble .ql-editor h1 {
  font-size: 30px;
  line-height: 40px;
  margin-bottom: 10px;
  font-weight: bold;
}

.rdp-element-text-two-column .ql-bubble .ql-editor h2 {
  font-size: 20px;
  line-height: 30px;
  font-weight: bold;
}

.rdp-element-text-two-column {
  padding-top: 35px;
  padding-bottom: 35px;
  border-top: 1px solid transparent;
  border-bottom: 1px solid transparent;
  overflow: hidden;
  position: relative;
}

.rdp-element-text-two-column:hover {
  /* border-top: 1px dashed #ccc; */
  /* border-bottom: 1px dashed #ccc; */
}

.rdp-element-text-two-column textarea, .rdp-element-text-two-column div.editor {
  width: 100%;
  max-width: 760px;
  display: block;
  margin: auto;
  border: 0;
  font-size: 18px;
  line-height: 26px;
  resize: none;
  overflow: hidden;
}

.rdp-element-text-two-column div.editor b {
  font-weight: bold;
}

.rdp-element-text-two-column .quill {
  /* width: 80%; */
  width: 100%;
  max-width: 760px;
  margin: auto;
}

.rdp-element-text-two-column-items {
  display: block;
  width: 100%;
  max-width: 760px;
  margin: auto;
}

.rdp-element-text-two-column-item {
  width: 50%;
  display: inline-block;
  vertical-align: top;
}

.rdp-element-text-two-column-item.first {
  padding-right: 15px;
}

.rdp-element-text-two-column-item.last {
  padding-left: 15px;
}

.tablet .rdp-element-text-two-column,
.mobile .rdp-element-text-two-column {
  padding-left: 20px;
  padding-right: 20px;
}

.tablet .rdp-element-text-two-column-item,
.mobile .rdp-element-text-two-column-item {
  width: 100%;
}

.tablet .rdp-element-text-two-column-item.first,
.mobile .rdp-element-text-two-column-item.first {
  padding-right: 0px;
  margin-bottom: 20px;
}

.tablet .rdp-element-text-two-column-item.last,
.mobile .rdp-element-text-two-column-item.last {
  padding-left: 0px;
}

.ql-format-custom-color {
  width: 28px;
  height: 24px;
  cursor: pointer;
  display: inline-block;
  background-image: url("./icon-paint.svg");
  background-size: 13px;
  background-position: center;
  background-repeat: no-repeat;
}

.ql-custom-color-picker {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 9999;
}

.ql-custom-color-picker-overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
} 

.ql-custom-color-picker .twitter-picker {
  position: fixed;
  /* top: 60px; */
  z-index: 2;
  /* right: 400px; */
  text-align: center;
  border: 0;
  border-bottom: 1px solid #f3f3f3;
  height: 80px;
  margin: 0;
  padding-top: 10px;
}

.ql-custom-color-picker .twitter-picker * {
  display: inline-block!important;
  float: none!important;
  vertical-align: top;
  line-height: 30px;
}

@media screen and (max-width: 768px) {
  .rdp-element-text-two-column {
    padding-left: 20px;
    padding-right: 20px;
  }

  .rdp-element-text-two-column-item {
    width: 100%;
  }
  
  .rdp-element-text-two-column-item.first {
    padding-right: 0px;
    margin-bottom: 20px;
  }
  
  .rdp-element-text-two-column-item.last {
    padding-left: 0px;
  }
}