.element-forum-1 {
  padding-top: 30px;
  padding-bottom: 30px;
  overflow: hidden;
  position: relative;
}

.element-forum-content {
  max-width: 760px;
  display: block;
  margin: auto;
  text-overflow: ellipsis;
}

.element-forum-1-content-header {
  display: flex;
  justify-content: flex-end;
  align-items: baseline;
  gap: 10px;
}

.element-forum-1-content-header-new-topic-btn {
  padding: 10px 20px;
  border-radius: 100px;
  border: 0;
  background: #acb6c1;
  color: #FFF;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 20px;
  cursor: pointer;
}

.element-forum-content-messages {

}

.element-forum-content-message {
  background-color: #e1f1eb;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 20px;
}

.element-forum-content-message-child {
  background-color: #f1f1f1;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 15px;
}

.element-forum-content-message-author {
  font-size: 12px;
  color: #A8A8A8;
  display: block;
  margin-bottom: 15px;
}

.element-forum-content-message-message {
  font-size: 14px;
  line-height: 18px;
  color: #727272;
  display: block;
  text-overflow: ellipsis;
  overflow-x: hidden;
}

.element-forum-content-message-actions {
  display: block;
  text-align: right;
  margin-top: 10px;
}

.element-forum-content-message-actions li {
  display: inline-block;
}

.element-forum-content-message-action {
  text-transform: uppercase;
  color: #00643e;
  font-size: 10px;
  font-weight: bold;
  display: inline-block;
  margin-left: 10px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  border-radius: 10px;
}

.element-forum-1-no-message {
  width: 100%;
  max-width: 760px;
  margin: auto;
  text-align: center;
  padding-bottom: 40px;
}

.element-forum-1-no-message-icon {
  width: 200px;
  height: 132px;
  display: block;
  margin: auto;
  background-image: url("./icon-no-message.png");
  background-size: 240px;
  background-position: center;
  background-repeat: no-repeat;
}

.element-forum-1-no-message-message {
  display: block;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  margin: 15px 0;
  color: #acb6c1;
}

.element-forum-content-message-children {}

.element-forum-1-no-message-new-topic-btn {
  padding: 10px 20px;
  border-radius: 100px;
  border: 0;
  background: #acb6c1;
  color: #FFF;
  font-weight: bold;
  text-transform: uppercase;
  margin-top: 20px;
}

@keyframes placeHolderShimmer{
  0%{
      background-position: -468px 0
  }
  100%{
      background-position: 468px 0
  }
}

.element-forum-1-preloader-linear-background {
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: placeHolderShimmer;
  animation-timing-function: linear;
  background: #f6f7f8;
  background: linear-gradient(to right, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);
  background-size: 1000px 104px;
  height: 338px;
  position: relative;
  overflow: hidden;
}

.element-forum-1-preloader {
  max-width: 760px;
  display: block;
  margin: auto auto 10px;
}

.element-forum-1-preloader-message {
  padding: 20px;
  border-radius: 10px;
  background-color: #f7f7f7;
}

.element-forum-1-preloader-message-author {
  width: 160px;
  height: 12px;
  margin-bottom: 15px;
  overflow: hidden;
  border-radius: 4px;
}

.element-forum-1-preloader-message-message {
  display: block;
  height: 14px;
  overflow: hidden;
  border-radius: 4px;
  margin-bottom: 10px;
}

.element-forum-1-preloader-message-actions {
  display: block;
  text-align: right;
}

.element-forum-1-preloader-message-actions-item {
  width: 120px;
  height: 12px;
  overflow: hidden;
  border-radius: 4px;
  display: inline-block;
}

.element-forum-1-preloader-message-children {}

.element-forum-1-preloader-message-child {
  padding: 20px;
  border-radius: 10px;
  background-color: #f7f7f7;
  margin-top: 15px;
}

.element-forum-content-message-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 15px;
}

.element-forum-content-message-header-options {
  display: flex;
  justify-content: center;
  align-items: center;
}

/*region Buttons states*/
.element-forum-1-no-message-new-topic-btn:focus,
.element-forum-1-content-header-new-topic-btn:focus {
  outline: 2px solid #acb6c1 !important;
  outline-offset: 2px;
}


.element-forum-1-no-message-new-topic-btn:hover,
.element-forum-1-content-header-new-topic-btn:hover {
  filter: saturate(1.5);
}

.element-forum-1-no-message-new-topic-btn:disabled,
.element-forum-1-content-header-new-topic-btn:disabled {
  cursor: not-allowed;
  filter: grayscale(1);
}
/*endregion Buttons states*/

.load-more-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}