.element-divider-numbered {
  padding-top: 30px;
  padding-bottom: 30px;
  border-top: 1px solid transparent;
  border-bottom: 1px solid transparent;
  width: 100%;
  display: block;
  margin: auto;
  overflow: hidden;
  position: relative;
}

.element-divider-numbered:hover {
  border-top: 1px dashed #ccc;
  border-bottom: 1px dashed #ccc;
}

.element-divider-numbered-content {
  width: 100%;
  max-width: 760px;
  display: block;
  margin: auto;
  position: relative;
  height: 50px;
  padding-top: 25px;
}

.element-divider-numbered-line {
  width: 100%;
  height: 1px;
  background-color: #e6e6e6;
}

.element-divider-numbered-circle {
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-radius: 100px;
  text-align: center;
  vertical-align: top;
  position: absolute;
  left: 50%;
  margin-left: -25px;
  top: 0px;
  font-weight: bold;
}